export default {
	data() {
		return {

		}
	},
	methods: {
		onSubmit(e) {
			let form = e.detail.value
			form.user_type = "C"
			form.user_status = "H"
			uni.request({
				url: 'http://localhost:8088/user/register',
				data: form,
				method: "POST"
			}).then((res) => {
				if(res[res.length-1].data.code === 0){
					uni.redirectTo({
						url:'../login/login'
					})
				}
			}).catch((e) => {
				console.log(e)
			})
		},
		reset() {}
	}
}
